$pageColor: firebrick;
$inputSize: 5vh;
body {
    background-color: $pageColor;
    margin: 0px;
}

.header-text {
    text-align: left;
    position: relative;
    top: 1.5rem;
    color: white;
    letter-spacing: 2px;
    .heading {
        font-size: 2rem;
    }
    .subtext {
        margin-top: -1rem;
        font-size: 0.8rem;
    }
}

.content {
    display: none;
    .landing-image {
        width: 100%;
    }
}

.landing-inputs {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.register-link {
    text-decoration: none;
    color: white;
}

.main-container {
    width: 65%;
    margin: auto;
    max-width: 500px;
    max-height: 100%;
    display: flex;
}

.form {
    text-align: center;
    width: 100%;
}

.label-title {
    display: none
}

.data-input,
.login-btn {
    width: 95%;
    height: $inputSize;
    margin: 0;
    padding: 5px;
    margin-top: 1.5vh;
    border-radius: 50px;
    border-radius: 50px;
    border-radius: 50px;
    border-color: white;
    background-color: $pageColor;
    font-size: 1rem;
}

.login-btn {
    width: 100%;
    border-radius: 50px;
    color: red;
    background-color: white;
    font-weight: bold;
    letter-spacing: 1px;
    padding: 5px;
    font-size: 1rem;
    height: 44.4px;
    position: relative;
    top: 4vh;
}

#terms {
    width: auto;
    height: auto;
    color: white;
}

.form>input::placeholder {
    color: white;
}

.terms-container {
    color: white;
    font-size: 10px;
    position: relative;
    left: 2px;
    top: 0.5rem;
}

.divider {
    color: white;
}

//removing outline when input is on focus
.form>input,
button,
select,
textarea {
    color: white;
    outline: none;
}


/* Changing AutoFill Background Color and Text Color */

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    box-shadow: 0 0 0 30px firebrick inset;
    color: white;
}

input:-webkit-autofill {
    -webkit-text-fill-color: white;
}

.footer {
    .icon {
        width: 16px;
        height: 16px;
    }
}

.social-container .divider {
    margin-top: 10vh;
    display: flex;
    hr {
        width: 40%;
    }
}

.social-links {
    display: flex;
    justify-content: space-around;
    padding-top: 10px;
    .icon {
        width: 30px;
    }
}

.login-social-msg {
    display: none;
}

.login-btn:hover,
.login-btn:focus {
    cursor: pointer;
}

.social-links {
    .social-btn {
        background-color: transparent;
        border: 0px;
        .text {
            display: none;
        }
    }
}

@media only screen and (min-width: 992px) {
    body {
        background-color: white;
    }
    .input-group {
        //background-color: red;
        display: flex;
        margin: 3vh 0px;
    }
    .heading {
        font-size: 2rem;
    }
    .subtext {
        font-size: 1vw;
        width: 60%;
    }
    .whole-page {
        display: flex;
        justify-content: center;
        height: 100vh;
        background-image: linear-gradient( to right bottom, #f2c42f, #ffa034, #ff7b49, #ff5861, #ea3c7b);
    }
    .landing-inputs .header-text .register-link {
        color: blue;
    }
    .main-container {
        width: 75vw;
        max-width: none;
        height: 75vh;
        display: flex;
        flex-direction: row;
    }
    .form {
        text-align: center;
        margin: auto;
        width: 80%;
        display: flex;
        flex-direction: column;
        position: relative;
        bottom: 20px;
    }
    .data-input {
        height: 1.5vh;
        margin-top: 0vh;
        font-size: 1rem;
        background-color: white;
        border-radius: 0px;
        outline: 0;
        border-width: 0 0 1px;
        border-color: black;
    }
    .form>input {
        color: black;
        outline: none;
    }
    .login-btn {
        display: block;
        text-align: center;
        width: 25%;
        height: 4vh;
        background-color: red;
        color: white;
        font-size: 10px;
        position: relative;
        z-index: 2;
    }
    .label-title {
        display: inline;
        text-align: left;
        width: 5rem;
        border-bottom: 1px solid black;
    }
    .form {
        color: black;
    }
    .data-input {
        color: black;
    }
    .login-container {
        display: flex;
        justify-content: flex-end;
    }
    input:-webkit-autofill,
    input:-webkit-autofill:hover,
    input:-webkit-autofill:focus,
    input:-webkit-autofill:active {
        -webkit-box-shadow: 0 0 0 30px white inset;
        color: black;
    }
    .form {
        input::placeholder {
            color: transparent;
        }
    }
    input:-webkit-autofill {
        -webkit-text-fill-color: black !important;
    }
    .content {
        display: block;
        position: relative;
        text-align: center;
        color: white;
        width: 50%;
        .landing-image {
            width: 100%;
            height: 100%;
            -webkit-border-top-left-radius: 10px;
            -webkit-border-bottom-left-radius: 10px;
            -moz-border-radius-topleft: 10px;
            -moz-border-radius-bottomleft: 10px;
            border-top-left-radius: 10px;
            border-bottom-left-radius: 10px;
        }
    }
    .content-text {
        text-align: left;
        height: fit-content;
        position: absolute;
        top: 30%;
        left: 35%;
        transform: translate(-50%, -50%);
        .content-h1 {
            font-size: 5rem;
            margin-top: 0px;
            margin-bottom: 0px;
        }
        .content-p {
            font-size: 1rem;
            position: relative;
            top: -10px;
        }
    }
    .landing-inputs {
        width: 50%;
        background-color: white;
        height: 100%;
        -webkit-border-top-right-radius: 10px;
        -webkit-border-bottom-right-radius: 10px;
        -moz-border-radius-topright: 10px;
        -moz-border-radius-bottomright: 10px;
        border-top-right-radius: 10px;
        border-bottom-right-radius: 10px;
    }
    .header-text {
        color: black !important; // I used !important here because this header continues to inherit the white colour from the mobile design.
        letter-spacing: 2px;
        width: 80%;
        display: block;
        margin: auto;
        position: relative;
        top: -10px;
        .heading {
            font-size: 2rem;
        }
        .subtext {
            font-size: 1vw;
            width: 60%;
        }
    }
    .terms-container {
        color: black;
        font-size: 14px;
        display: flex;
        justify-content: start;
        position: relative;
        .terms-input {
            margin-right: 3%;
        }
    }
    .social-container .divider {
        display: none;
    }
    .msg-icons {
        text-align: left;
        display: flex;
        flex-direction: column;
        padding-bottom: 8%;
        span {
            position: relative;
        }
    }
    .login-social-msg {
        display: flex;
        z-index: 1;
    }
    .social-links {
        position: relative;
        top: 3rem;
        .social-btn {
            background-color: transparent;
            border: 0px;
            display: flex;
            align-items: center;
            border-radius: 30px;
            width: 7vw;
            color: white;
            .text {
                display: block;
                width: 50%;
                font-size: 9px;
            }
        }
        button:hover,
        button:focus {
            cursor: pointer;
        }
    }
    .social-btn {
        color: white;
        &.fb {
            background-color: #4267b2;
        }
        &.tw {
            background-color: #1da1f2;
        }
        &.gp {
            background-color: #dd4b39;
        }
    }
}